<template>
  <div>
    <el-menu
      class="el-menu-vertical"
      @open="handleOpen"
      @close="handleClose"
      background-color="#2c2c2c"
      text-color="#fff"
      router
      unique-opened
      :collapse="isCollapse"
      :default-active="$route.path"
    >
      <div class="logo">
        <img
          class="logo-img-small"
          v-if="isCollapse"
          src="data:image/svg+xml;base64,"
          alt="logo"
        />
        <img
          class="logo-img"
          v-else
          src="data:image/svg+xml;base64,"
          alt="logo"
        />
      </div>
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-data-line"></i>
          <span slot="title">采购价格看板管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="/">发布物料</el-menu-item>
          <el-menu-item index="/manage">物料管理</el-menu-item>
          <el-menu-item index="1-3" disabled>报价看板</el-menu-item>
          <el-menu-item index="1-4" disabled>购物车</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-goods"></i>
          <span slot="title">商品管理</span>
        </template>
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
          <i class="el-icon-document"></i>
          <span slot="title">订单管理</span>
        </template>
      </el-submenu>
      <el-submenu index="4">
        <template slot="title">
          <i class="el-icon-star-off"></i>
          <span slot="title">收藏管理</span>
        </template>
      </el-submenu>
      <el-submenu index="5">
        <template slot="title">
          <i class="el-icon-office-building"></i>
          <span slot="title">企业信息</span>
        </template>
      </el-submenu>
      <el-submenu index="6">
        <template slot="title">
          <i class="el-icon-office-building"></i>
          <span slot="title">注册开户</span>
        </template>
      </el-submenu>
      <el-submenu index="7">
        <template slot="title">
          <i class="el-icon-office-building"></i>
          <span slot="title">支付管理</span>
        </template>
      </el-submenu>
      <el-submenu index="8">
        <template slot="title">
          <i class="el-icon-office-building"></i>
          <span slot="title">交易豆管理</span>
        </template>
      </el-submenu>
    </el-menu>
    <div class="sidebar-btn sidebar-open-btn" @click="handleClick" v-if="isCollapse">
      <i class="el-icon-s-unfold"></i>
    </div>
    <div class="sidebar-btn sidebar-close-btn" @click="handleClick" v-else>
      <i class="el-icon-s-fold"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "SideBar",
  data() {
    return {
      isCollapse: false
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClick() {
      this.isCollapse = !this.isCollapse;
    }
  }
};
</script>

<style scoped>
.el-menu-vertical {
  height: 100%;
  min-height: 100vh;
}

.el-menu-vertical:not(.el-menu--collapse) {
  width: 200px;
}

.logo-img {
  padding: 35px 27px 25px;
}

.logo-img-small {
  width: 64px;
  border-right: 1px solid #fff;
}

.sidebar-btn {
  position: absolute;
  top: 38px;
  font-size: 24px;
}

.sidebar-open-btn {
  left: 80px;
}

.sidebar-close-btn {
  left: 220px;
}
</style>



